import { axiosClient } from "@rp/react/axios-client"
import { FILES_CONTENTS_OWNERS_ROUTE } from "../file.constants"
import { PrivateSharedContentOwner } from "../file.types"

async function getAllPrivateFilesOwners(): Promise<Array<PrivateSharedContentOwner>> {
  const [
    {
      data: { results: userResults },
    },
    {
      data: { results: othersResults },
    },
  ] = await Promise.all([
    axiosClient.get(`${FILES_CONTENTS_OWNERS_ROUTE}?shared=false`),
    axiosClient.get(`${FILES_CONTENTS_OWNERS_ROUTE}?shared=true`),
  ])

  const sortedCombinedData = [...userResults, ...othersResults].sort(
    (a: PrivateSharedContentOwner, b: PrivateSharedContentOwner) =>
      a.fullName.localeCompare(b.fullName),
  )

  return sortedCombinedData
}

export { getAllPrivateFilesOwners }
